<template>
    <div id="game_timer">
        <timer-setup v-if="!time" @set-time="setTime"></timer-setup>
	    <div v-else>
            <div class="timer">{{ prettyTime | prettify }}</div>
            <div>
                <button v-if="!isRunning" @click="start">Start</button>
                <button v-if="isRunning" @click="stop">Stop</button>
                <button @click="reset">Reset</button>
            </div>
	    </div>   
    </div>
</template>

<script>

import TimerSetup from './TimerGame/TimerSetup.vue'
export default {
    name: 'game_timer',
    components:{
        TimerSetup
    },
    data(){
        return{
            isRunning: false,
            minutes: 0,
            secondes: 0,
            time: 0,
            timer:null
        }
    },
    methods:{
        convertTime(ms){
            let a = ms.split(':')
            return ((a[0]*60)+(+a[1]))
        },
        start(){
            this.isRunning = true
            if(!this.timer){
                this.timer = setInterval(()=>{
                    if(this.gameTime>0){
                        this.gameTime--
                    } else {
                        clearInterval(this.timer)
                        this.reset()
                    }
                }, 1000)
            }
        },
        stop () {
			this.isRunning = false
			clearInterval(this.timer)
			this.timer = null
		},
		reset () {
			this.stop()
			this.time = 0
			this.secondes = 0
			this.minutes = 0
		},
		setTime (payload) {
			this.time = (payload.minutes * 60 + payload.secondes)
		}
    },
    computed: {
        prettyTime(){
            let time = this.time / 60
            let minutes = parseInt(time)
            let secondes = Math.round((time - minutes) *60)
            return minutes+":"+secondes
        }
    },
    filters: {
        prettify : function(value) {
            let data = value.split(':')
            let minutes = data[0]
            let secondes = data[1]
            if (minutes < 10) {
                minutes = "0"+minutes
            }
            if (secondes < 10) {
                secondes = "0"+secondes
            }
            return minutes+":"+secondes
        }
	}
    
    // mounted: function(){
    //     this.$root.$on('startGame', (data)=>{
    //         if(data.game_status == true){
    //             this.startTime()
    //         }
    //     }),
    //     this.$root.$on('pauseGame', (data)=>{
    //         if(data.game_status == false){
    //             this.isRunning = false
    //             clearInterval(this.timerId)
    //             this.timerId = null
    //         }
    //     })
    // }

}
</script>

<style scoped>
  .time p{
    font-weight: bold;
    
  }
  .time span{
    font-weight: bold;
    font-size: 35px;
  }
</style>
